import {isDone, isPending} from "./types";

export const getStatus = (statusId: string) => {
    let status = '';
    switch (statusId) {
        case isDone:
            status = '';
            break;
        case isPending:
            status = '';
            break;
    }
    return status;
}

export const cleanToken = (token: string | null) => {
    return token && token.replace(/^Bearer /, '');
}
